}

svg {
	width: 40px;
	height: 40px;
	fill: #ebbcba;
	transition: all 0.2s ease-in-out;
}

svg:hover {
	fill: #efc9c8;
	-webkit-transform: scale(1.2);
	transform: scale(1.2);
	cursor: pointer;
}

.line {
	border-right: 1px solid #efc9c8;
	color: transparent;
	height: 200px;
	margin-left: -10px;
}

/* For smarphones and tablets */
@media only screen and (max-width: 834px) {
