#animated {
	opacity: 0;
	transition: opacity 1.2s ease-in-out;
	height: 100vh;
	overflow: hidden;
}

.content {
	display: flex;
	flex-direction: row;
	height: 100%;
}

main {
	width: 100%;
	display: flex;
	flex-direction: column;
	font-size: 50px;
	-ms-overflow-style: none; /* not display scrollbar for IE and Edge */
	scrollbar-width: none; /* not display scrollbar for Firefox */
	overflow-y: scroll;
	padding: 120px 200px 0px 300px;
}

main::-webkit-scrollbar {
    display: none; /* not display scrollbar for Chrome, Safari and Opera */
}


/* For smartphones and tablets */
@media only screen and (max-width: 834px) {
	main {
		font-size: 20px;
	}
}

#loader {
	position: absolute;
	width: 600px;
	height: 36px;
	left:50%;
	top:40%;
	margin-left: -300px;
	overflow: visible;
	-webkit-user-select: none;
	user-select: none;
	cursor: default;
}

#loader > div {
	position: absolute;
	width: 50px;
	height: 36px;
	opacity: 0;
	animation: move 5s linear;
	-o-animation: move 5s linear;
	-moz-animation: move 5s linear;
	-webkit-animation: move 5s linear;
	transform: rotate(180deg);
	-o-transform: rotate(180deg);
	-moz-transform: rotate(180deg);
	-webkit-transform: rotate(180deg);
	color: #eb6f92;
}

#loader > div:nth-child(2) {
	animation-delay: 0.2s;
	-o-animation-delay: 0.2s;
	-moz-animation-delay: 0.2s;
	-webkit-animation-delay: 0.2s;
}

#loader > div:nth-child(3) {
	animation-delay: 0.4s;
	-o-animation-delay: 0.4s;
	-webkit-animation-delay: 0.4s;
	-webkit-animation-delay: 0.4s;
}

#loader > div:nth-child(4) {
	animation-delay: 0.6s;
	-o-animation-delay: 0.6s;
	-moz-animation-delay: 0.6s;
	-webkit-animation-delay: 0.6s;
}

#loader > div:nth-child(5) {
	animation-delay: 0.8s;
	-o-animation-delay: 0.8s;
	-moz-animation-delay: 0.8s;
	-webkit-animation-delay: 0.8s;
}

#loader > div:nth-child(6) {
	animation-delay: 1s;
	-o-animation-delay: 1s;
	-moz-animation-delay: 1s;
	-webkit-animation-delay: 1s;
}

#loader > div:nth-child(7) {
	animation-delay: 1.2s;
	-o-animation-delay: 1.2s;
	-moz-animation-delay: 1.2s;
	-webkit-animation-delay: 1.2s;
}

@keyframes move {
	0% {
		left: 0;
		opacity: 0;
	}
	35% {
		left: 41%;
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg);
		opacity: 1;
	}
	65% {
		left: 59%; 
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg); 
		opacity: 1;
	}
	100% {
		left: 100%; 
		-webkit-transform: rotate(-180deg); 
		transform: rotate(-180deg);
		opacity: 0;
	}
}

@-webkit-keyframes move {
	0% {
		left: 0; 
		opacity: 0;
	}
	35% {
		left: 41%; 
		-webkit-transform: rotate(0deg); 
		transform: rotate(0deg); 
		opacity: 1;
	}
	65% {
		left: 59%; 
		-webkit-transform: rotate(0deg); 
		transform: rotate(0deg); 
		opacity: 1;
	}
	100% {
		left: 100%;
		-webkit-transform: rotate(-180deg); 
		transform: rotate(-180deg); 
		opacity: 0;
	}
}

